*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.light {
  --bg-header: #fff;
  --color-text-logo: #282828;
  --color-text-header: #595959;
  --color-text-menu: #111;
  --bg-mobile: #f8f8f8;
  --bg-primary: #fff;
  --bg-secondary: #f8f8f8;
  --bg-votes: #f8f8f8;
  --bg-dropdown: #f8f8f8;
  --color-text-primary: #111;
  --color-text-secondary: #282828;
  --color-text-info: #111;
  --color-text-data: #282828;
  --color-text-neutral: #595959;
  --color-text-vote: #282828;
  --color-shadow-popup: rgba(0, 0, 0, .22);
  --color-border-primary: #595959;
}

.dark {
  --bg-header: #000;
  --color-text-logo: #fff;
  --color-text-header: #b7b7b7;
  --color-text-menu: #fff;
  --bg-mobile: #171717;
  --bg-primary: #111;
  --bg-secondary: #111;
  --bg-votes: #fff;
  --bg-dropdown: #1c1c1c;
  --color-text-primary: #fff;
  --color-text-secondary: #f8f8f8;
  --color-text-info: #b7b7b7;
  --color-text-data: #fff;
  --color-text-neutral: #b7b7b7;
  --color-text-vote: #111;
  --color-shadow-popup: rgba(255, 107, 8, .42);
  --color-border-primary: #fff;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p {
