.slider {
  cursor: pointer;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

input:checked + .switch__marker {
  transform: translateX(-22px)rotate(-180deg);
}

@media screen and (min-width: 768px) {
  input:checked + .switch__marker {
    transform: translateX(-34px)rotate(-180deg);
  }
}

@media screen and (min-width: 1280px) {
  input:checked + .switch__marker {
    transform: translateX(-36px)rotate(-180deg);
  }
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.switch__icon-moon, .switch__icon-sun {
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
}

